.footer{
  background: #282D3C;
  color: $white;
  &__logo{
    display: block;
    width: 10.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__by{
    display: flex;
    align-items: center;
    margin: 2em 0 -3em;
    svg{
      width: 2.5em;
      margin-right: .75em;
    }
  }
}

.footer-top{
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  .socials{
    margin-top: 1.25em;
  }
}

.footer-nav{
  ul{
    @include grid100gap(.94em);
  }
  a{
    font-size: .875em ;
    line-height: 135%;
  }
}

.footer-contacts{
  text-align: right;
  white-space: nowrap;
  &__call{
    margin-bottom: .625em;
  }
}

.footer-bottom{
  padding: 1.44em 0;
  background: #1D212E;
  text-align: center;
  &__info{
    span{
      color: #6683A3;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__by{
      margin-bottom: 0;
    }
  }

  .footer-top {
    &__inner {
      @include grid100gap(1.5em);
    }
    &__btn{
      width: 100%;
    }
  }

  .footer-contacts{
    text-align: left;
  }

  .footer-bottom{
    text-align: left;
  }
}


