.catalog{
  padding: 1.38em 0 5.31em;
  .h2{
    max-width: 30em;
  }
  &__filter-btn{
    display: none;
  }
  .services-nav{
    @media screen and (min-width: 993px){
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
      &__link{
        margin: 0;
      }
    }
  }
  &__text{
    margin-top: 2.44em;
    p{
      font-size: 1em;
      line-height: 162%;
    }
    &:not(._show-all){
      p~p{
        display: none;
      }
      p{
        display: inline;
      }
    }
    &._show-all{
      .catalog__text-toggle{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &-toggle{
      color: $primary2;
      &:hover{
        color: $black;
      }
      span{
        font-size: 1em;
        line-height: 162%;
      }
      ._view2{
        display: none;
      }
    }
  }
  &__items{
    margin-top: 2.19em;
  }
  &__nav{
    margin-top: 3em;
  }
}

.filter{
  padding: 1.875em;
  @include card;
  @include grid100gap(1.625em);
  &__bg{
    display: none;
  }
}

.filter-categories{
  display: none;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__link{
    font-size: .875em;
    line-height: 185%;
    color: $dark;
    margin-bottom: 1em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.filter-content{
  &__groups{
    margin-top: 1.25em;
    @include grid100gap(1.5em);
  }
}

.filter-group{
  &__title{
    font-size: 1em;
    line-height: 162%;
    font-weight: 600;
    color: $dark;
  }
  &__checks{
    margin-top: .875em;
    @include grid100gap(.875em);
  }
  .filter-slider{
    margin-top: .875em;
  }
}

.product-card{
  color: #3A526D;
  padding: 1.875em;
  @include card;
  transition: all .3s;
  &:hover{
    box-shadow: $shadow;
  }
  &__img{
    @include imgCover(100%);
    margin-bottom: .94em;
  }
  &__prices{
    margin-top: .94em;
  }
  &__price,
  &__price2{
    font-size: 1.25em;
    line-height: 140%;
    font-weight: 600;
  }
  &__price{
    color: $primary2;
  }
  &__price2{
    color: #727272;
    margin-right: .67em;
    text-decoration: line-through;
  }
  &__nav{
    margin-top: auto;
    padding-top: .94em;
  }
  &__link{
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__filter-btn{
      margin: -2.375em 0 0 auto;
      @include center;
      width: 1.25em;
      height: 1.25em;
      position: relative;
      img{
        width: 100%;
      }
      span{
        position: absolute;
        font-size: .5em;
        height: 1.75em;
        min-width: 1.75em;
        @include center;
        padding: 0 .25em;
        border-radius: 50%;
        background-color: $primary;
        color: $white;
        top: -.375em;
        right: -.75em;
      }
    }
    &__text{
      font-size: .875em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
  }

  .filter{
    transition: all .5s;
    transform: translateX(-110%);
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 18em;
    height: $vh;
    padding: 1em;
    border-radius: 0;
    overflow-y: auto;
  }
}


@media screen and (max-width: 576px){
  .catalog{
    .services__btn{
      width: calc(100% - 2.5em);
    }
  }

  .product-card{
    padding: 1.25em;
  }
}