.header-contacts,
.footer-contacts{
  &__phone{
    font-size: 1em;
    line-height: 118%;
  }
  &__tg,
  &__whatsapp{
    margin-left: .56em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__whatsapp{
    width: .875em;
  }
  &__tg{
    width: 1em;
  }
  &__email{
    font-size: .875em;
    line-height: 135%;
    color: $primary;
    &:hover{
      color: $yellow;
    }
  }
}

.header-contacts{
  display: flex;
  align-items: center;
  &__phone{
    font-weight: 600;
    min-width: 9.2em;
  }
}

.lang{
  position: relative;
  &._open{
    .lang__show svg{
      transform: rotate(180deg);
    }
    .lang__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    &:before{
      display: none;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 1em;
    background-color: $white;
    box-shadow: $shadow;
    border-radius: .5em;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__item{
    font-size: .875em;
    line-height: 120%;
    margin-bottom: .5em;
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.header-search{
  width: 26.75em;
  margin-right: .75em;
  position: relative;
  .fg{
    input{
      padding-right: 3em;
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1.4375em;
    width: 1.25em;
    @include center;
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .header-search{
    width: 100%;
    margin: 0;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .header-contacts{
    margin: 1em 0 1.25em;
  }
}


