.cart{
  padding: 1.19em 0 6em;
  &__inner{
    @include card;
    padding: 1.875em;
    margin-top: 1.19em;
  }
}

.cart-table{
  margin-top: .5em;
  color: #3A526D;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th,td{
    border-bottom: 1px solid #E8E8E8;
  }
  th{
    font-weight: 400;
    text-align: left;
    span{
      font-size: .75em;
      line-height: 233%;
    }
  }
  @media screen and (min-width: 993px){
    th,td{
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(5){
        width: 10.19em;
      }
      &:nth-child(4){
        width: 11.5em;
      }
      &:nth-child(6){
        width: 1.875em;
      }
    }
  }
}

.cart-row{
  td{
    padding: 1.25em 0;
  }
  &__img{
    @include imgCover(100%);
    padding: 0;
    width: 5.8125em;
    height: 5.8125em;
  }
  &__title{
    max-width: 25.5em;
  }
  &__price{
    font-size: 1.25em;
    line-height: 140%;
    color: #3E98D9;
    font-weight: 600;
  }
  &__remove{
    width: .875em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill-opacity: 1;
      }
    }
  }
  &__mob-title{
    display: none;
  }
}

.cart-sum{
  margin-top: 1.94em;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &__title{
    margin-right: 1em;
    color: #3A526D;
  }
  &__price{
    color: #3E98D9;
  }
}

.cart-contacts{
  margin-top: 2.19em;
  padding: 2em 1.875em;
  @include card;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
    margin-top: 1.75em;
  }
  .fg{

  }
  &__btn{
    margin-top: 1.5em;
  }
}

.fg-icon{
  textarea,
  input{
    font-size: .75em;
    padding-left: 4em;
    height: 4em;
    border-radius: .83em;
  }
  textarea{
    height: 10em;
    padding-left: 1.5em;
  }
  .fg__icon{
    position: absolute;
    left: 1.19em;
    top: 50%;
    transform: translateY(-50%);
    max-width: 1em;
    max-height: 1em;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .cart{
    &__inner{
      padding: 1em;
    }
  }

  .cart-table{
    thead{
      display: none;
    }
    tbody,
    table{
      display: block;
    }
    tr{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: .75em 1.25em;
    }
    td{
      padding: 0;
      display: block;
      border: none;
      @media screen and (min-width: 577px){
        &:nth-child(2){
          grid-column: span 2;
          width: calc(100% + 6.5em);
          margin-left: -6.5em;
        }
      }
    }
  }

  .cart-row{
    padding: 1em 0;
    border-top: 1px solid #E8E8E8;
    position: relative;
    &:last-child{
      border-bottom: 1px solid #E8E8E8;
    }
    &__title{
      max-width: 100%;
    }
    &__mob-title{
      display: block;
      margin: 0 0 .5em;
    }
    &__remove{
      position: absolute;
      top: 1em;
      right: 0;
    }
  }

  .cart-contacts{
    padding: 1em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      margin-top: 1em;
    }
    &__btn{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-table{
    tr{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
    td{
      &:nth-child(2){
        grid-column: span 2;
      }
    }
  }

  .cart-contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      padding: 0;
      width: 100%;
      height: 3em;
      span{
        font-size: .75em;
      }
    }
  }
}