.advantages{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .75em));
    gap: 1em;
  }
}

.advantage-card{
  padding: 2.5em;
  border-radius: 1.0625em;
  background-color: $dark;
  color: $white;
  display: flex;
  transition: all .3s;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background-image: url(../img/round.svg);
    width: 4.8125em;
    height: 4.8125em;
    @include bgImgSize(100%);
    right: 2.78em;
    bottom: 2.5em;
    transition: all .3s;
    opacity: 0;
  }
  &:hover{
    box-shadow: 0px 7px 16px 0px rgba(132, 163, 201, 0.33);
    &:before{
      opacity: 1;
    }
  }
  &:nth-child(4n-2){
    background-color: #376092;
  }
  &:nth-child(4n-1){
    background-color: #6B95C9;
  }
  &:nth-child(4n){
    background-color: #84A3C9;
  }
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: -.5em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    line-height: 164%;
  }
}

.equip{
  &__items{
    @include grid100gap(2em);
  }
}

.equip-card{
  padding: 2.5em 2.5em 2.5em 0;
  display: grid;
  grid-template-columns: 22.125em calc(100% - 24.625em);
  gap: 2.5em;
  align-items: center;
  border-radius: .75em;
  background-color: $white;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    color: #3A526D;
  }
  &__tags{
    margin-top: 1em;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  &__tag{
    display: flex;
    align-items: center;
    padding: .625em;
    border-radius: 0.9375em;
    background: #F5F5F5;
    max-width: 23.75em;
    margin: .69em .69em 0 0;
    color: $primary;
    span{
      font-size: .8125em;
      line-height: 115%;
    }
    &:hover{
      background-color: $primary;
      color: $white;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.625em;
  }
}


@media screen and (max-width: 992px){
  .advantages{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
  }

  .equip{
    &__items{
      gap: 1em;
    }
  }

  .equip-card{
    padding: 1em 1.25em;
    grid-template-columns: 17em calc(100% - 18em);
    gap: 1em;
    &__title{
      color: #3A526D;
    }
    &__tags{
      margin-top: .25em;
    }
    &__nav{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .advantage-card{
    border-radius: .75em;
    padding: 1.25em;
  }

  .equip-card{
    grid-template-columns: 100%;
    &__title{
      line-height: 130%;
    }
  }
}
