.certificate-card{
  display: block;
  box-shadow: $shadow2;
  border-radius: .75em;
  padding: .625em;
  background: #282D3C;
  &__inner,
  &__img{
    box-shadow: $shadow2;
    border-radius: inherit;
  }
  &__inner{
    background: radial-gradient(65% 65% at 50% 50%, #F8F8F8 87%, #EDEDED 100%);
    padding: .625em;
  }
  &__img{
    @include imgCover(149.6%);
  }
}

.news-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      }
    }
  }
}

.news-card{
  height: 100%;
  padding: 2.5em;
  border-radius: 0.75em;
  background-color: $white;
  transition: all .3s;
  &:hover{
    box-shadow: $shadow;
  }
  &__img{
    @include imgCover(61%);
    border-radius: .3125em;
  }
  &__content{
    margin-top: .625em;
    @include grid100gap(.625em);
  }
  &__date{
    font-size: .8125em;
    line-height: 215%;
    color: #84A3C9;
  }
  &__title{
    a{
      text-decoration: underline;
      text-decoration-color: transparent;
      &:hover{
        color: #3E98D9;
        text-decoration-color: #3E98D9;
      }
    }
  }
  &__desc{
    color: #485565;
    @include truncate(4);
  }
}

.m-round{
  pointer-events: none;
  width: 18.8125em;
  height: 18.8125em;
  background: linear-gradient(0deg, #FFF 0%, #FFF 100%), #FFF;
  border-radius: 50%;
}

.main{
  margin-top: -8.75em;
}

.main-slider{
  .swiper{
    &-button{
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: -11.375em;
      }
      &-next{
        right: -11.375em;
      }
    }
  }
  .swiper-slide{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &-active{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .swiper-pagination{
    position: absolute;
    bottom: 8.125em;
  }
}

.main-slide{
  min-height: 50.25em;
  position: relative;
  display: flex;
  align-items: center;
  padding: 10em 0;
  &__content{
    max-width: 36.25em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 160%;
    color: #3A526D;
    margin-top: 1.2em;
  }
  &__desc2{
    line-height: 225%;
    margin-top: 1.44em;
  }
  &__nav{
    margin-top: 1.44em;
    display: flex;
  }
  &__img{
    z-index: 2;
    position: absolute;
    right: -20em;
    top: 50%;
    transform: translateY(-45%);
    width: 63em;
    img{
      width: 100%;
      display: block;
    }
  }
  .m-round{
    position: absolute;
  }
  &__round1{
    top: 9.88em;
    left: -8em;
  }
  &__round2{
    width: 3.625em;
    height: 3.625em;
    top: 10.38em;
    right: -12.625em;
  }
  &__round3{
    width: 2em;
    height: 2em;
    top: 15em;
    left: 35em;
  }
}

.main-video{
  position: absolute;
  left: 36.3125em;
  bottom: 15.5625em;
  z-index: 3;
  &__bg,
  &__text{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .625em;
    width: 14.25em;
    height: 3.75em;
    padding: 0 3.44em;
  }
  &__text{
    display: flex;
    align-items: center;
    color: #282D3C;
    white-space: nowrap;
    z-index: 3;
  }
  &__bg{
    background: #FFD873;
    border-radius: 3.75em;
  }
  .play-btn{
    &:after{
      content: '';
      @include centerAbsolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      z-index: 2;
      background-color: inherit;
      transition: all .3s;
    }
    img{
      z-index: 3;
      position: relative;
    }
  }
  &:hover{
    transform: scale(1.1);
  }
}

.play-btn{
  transition: all .3s;
  width: 5em;
  height: 5em;
  background-color: #FFC555;
  border-radius: 50%;
  @include center;
  position: relative;
  &:before{
    content: '';
    @include centerAbsolute;
    width: 147%;
    height: 147%;
    border-radius: 50%;
    border: 1px dashed rgba(132, 163, 201, 0.57);
  }
  &:hover{
    background-color: $yellow;
  }
  img{
    width: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .news{
    &__inner{
      position: relative;
      padding-bottom: 5em;
    }
    &__link{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .news-card{
    padding: 1.25em;
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .main{
    overflow: hidden;
    margin-top: -10.25em;
  }

  .main-slider{
    .swiper-button{
      display: none;
    }
    .swiper-pagination{
      bottom: 2em;
    }
  }

  .main-slide{
    padding: 11em 0 5em;
    display: block;
    min-height: unset;
    .m-round,
    &__img{
      display: none;
    }
  }

  .main-video{
    bottom: 5em;
    left: unset;
    right: 2em;
    margin-top: 2em;
    &__text,
    &__bg{
      display: none;
    }
  }

  .play-btn{
    font-size: .75em;
  }
}


@media screen and (max-width: 576px){
  .main-slide{
    &__desc{
      font-size: 1em;
      margin-top: 1em;
    }
    &__desc2{
      line-height: 130%;
    }
  }
}
