.projects{
  &__items{
    @include grid100gap(2.5em);
  }
  &__nav{
    margin-top: 4.375em;
  }
}

.project-card{
  padding: 2.125em;
  background-color: $white;
  border-radius: .75em;
  box-shadow: $shadow2;
  .photo-slider{
    margin-top: 1.44em;
  }
  &__text{
    margin-top: 1.75em;
    line-height: 187%;
    p{
      line-height: 187%;
    }
  }
  &__nav{
    margin-top: 1.875em;
  }
  &__btn{
    margin-right: 1.1875em;
    &:last-child{
      margin: 0;
    }
  }
}

.video{
  display: block;
  position: relative;
  .play-btn{
    @include centerAbsolute;
  }
}


@media screen and (max-width: 992px){
  .projects{
    &__items{
      gap: 1.25em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .project-card{
    padding: 1.25em;
    .photo-slider{
      margin-top: 1em;
    }
    &__text{
      line-height: 160%;
      margin-top: 1em;
      p{
        line-height: 160%;
      }
    }
    &__nav{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .project-card{
    &__nav{
      display: block;
    }
    &__btn{
      margin: 0 0 .75em;
    }
  }
}