.catalog-item{
  padding: 1.25em 0 6em;
  &__inner{
    padding: 2.5em;
    border-radius: 0.75em;
    background: $white;
    box-shadow: $shadow2;
    margin-top: 1.5em;
  }
  &__top{
    display: grid;
    grid-template-columns: 35.4375em calc(100% - 37.6875em);
    gap: 1.25em;
  }
  .photo-slider{
    &__3d{
      position: absolute;
      top: 1.875em;
      right: 1.875em;
      z-index: 2;
      width: 2em;
      img{
        width: 100%;
      }
    }
    &__img{
      padding-bottom: 100%;
    }
    .swiper{
      &-button{
        &-prev{
          left: 1.94em;
        }
        &-next{
          right: 1.94em;
        }
      }
      &-pagination{
        bottom: 1.5em;
      }
    }
  }
  &__delivery{
    margin-top: 2.875em;
    .text{
      font-weight: 400;
    }
  }
}

.props{
  @include grid100gap(.7em);
  &__link{
    font-size: 1em;
    line-height: 150%;
    display: inline-flex;
    margin-top: .7em;
    color: #3E98D9;
    border-bottom: 1px dashed #3E98D9;
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 150%;
  }
  &__title{
    color: #3A526D;
  }
  &__desc{
    color: $primary;
    a{
      text-decoration: underline;
    }
  }
}

.product-cart{
  margin-top: 1.375em;
  &__price{
    font-size: 1.25em;
    line-height: 140%;
    color: #3E98D9;
  }
  &__btn,
  .cartcalc{
    margin-left: 1.25em;
  }
}

.product-desc{
  margin-top: 2.125em;
  .text{
    margin-top: .69em;
    line-height: 181%;
    font-weight: 400;
    color: #3A526D;
    p{
      line-height: 181%;
    }
  }
}

.product-props{
  margin-top: 2.81em;
  .props{
    margin-top: .875em;
  }
  .prop-item{
    padding: .125em .81em;
    &:nth-child(odd){
      background: #F8F8F8;
    }
  }
}

.product-advantages{
  margin-top: 2.81em;
  .advantages__grid{
    margin: 1em 0 0 -2.5em;
    width: calc(100% + 5em);
  }
  .advantage-card{
    background-color: transparent !important;
    color: $dark;
    pointer-events: none;
    &:before{
      display: none;
    }
    @media screen and (min-width: 993px){
      &:nth-child(2){
        padding-right: 1.81em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding-bottom: 3em;
    &__inner{
      padding: 1.25em;
      width: calc(100% + 2em);
      margin-left: -1em;
      margin-top: 1em;
    }
    &__top{
      grid-template-columns: 100%;
    }
    &__delivery{
      margin-top: 1.5em;
    }
  }

  .product-advantages {
    .advantages__grid {
      margin-left: -1.25em;
      width: calc(100% + 2.5em);
    }
    .advantage-card{
      padding: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__delivery{
      .text{
        font-size: .875em;
      }
    }
  }

  .props{
    &__link{
      font-size: .875em;
    }
  }

  .prop-item{
    font-size: .875em;
  }

  .product-cart{
    flex-wrap: wrap;
    &__btn{
      width: 100%;
      margin: .75em 0 0;
    }
  }

  .product-desc{
    margin-top: 1.75em;
    .text{
      font-size: .875em;
    }
  }

  .product-props{
    margin-top: 1.75em;
  }
}
