.contacts{
  padding-top: 1.19em;
  &__desc{
    margin-top: .875em;
  }
  &__items{
    margin-top: 2.5em;
  }
  .requisites-group{
    padding: 2em;
    @include card;
  }
}

.contacts-bottom{
  margin-top: 2.5em;
  position: relative;
  &__wrap{
    color: $dark;
    padding: 2.375em;
    @include card;
    position: absolute;
    right: calc((100vw - 1648px) / 2);
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    width: 31.5em;
    @include grid100gap(1.69em);
    @media screen and (max-width: 1648px){
      right: 12.75em;
    }
  }
  &__group{
    @include grid100gap(.56em);
  }
  &__title{
    color: #84A3C9;
  }
  &__time,
  &__address{
    font-size: 1em;
    line-height: 150%;
    max-width: 22.375em;
    .underline-text{
      white-space: nowrap;
      color: #6B95C9;
    }
  }
  &__phone{
    white-space: nowrap;
    font-size: 1.4375em;
    line-height: 122%;
    font-weight: 600;
  }
}

.map{
  height: 47em;
  width: 100%;
}


@media screen and (max-width: 992px){
  .contacts-bottom{
    &__wrap{
      position: static;
      width: calc(100% - 2em);
      margin: 0 auto 1em;
      transform: unset;
    }
  }

  .map{
    height: 30em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    .requisites-group{
      padding: 1.25em;
    }
  }

  .contacts-bottom{
    &__wrap{
      padding: 1.25em;
    }
  }
}