.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.text h1,
.h1{
  font-size: 2.875em;
  line-height: 133%;
  font-weight: 600;
}

.text h2,
.h2{
  font-size: 2.25em;
  line-height: 153%;
  font-weight: 600;
}

.text h4,
.h3{
  font-size: 1.25em;
  line-height: 160%;
  font-weight: 600;
}

.text h3,
.h4{
  font-size: 1.5625em;
  line-height: 140%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 155%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text li,
.text p,
.text16{
  font-size: 1em;
  line-height: 175%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 135%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.blue-text{
  color: $primary;
}

.yellow-text{
  color: $yellow;
}

.dark-text{
  color: $dark;
}



@media screen and (max-width: 992px){
  .h1,
  .h2{
    font-size: 2em;
    line-height: 130%;
  }

  .h2-small-mob{
    font-size: 1.75em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
