.become-partner{
  padding: 1.19em 0 3.875em;
  &__desc{
    margin-top: .875em;
  }
  .text{
    margin: 3.875em auto 0;
    width: 51.625em;
    max-width: 100%;
  }
  &__info{
    display: grid;
    grid-template-columns: 25em calc(100% - 28.125em);
    gap: 3.125em;
  }
}

.text-big{
  font-weight: 400;
  line-height: 167%;
  p,li{
    font-size: 1.125em;
    line-height: 167%;
  }
  p{
    margin-bottom: 0;
  }
}

.conf{
  padding: 1.19em 0 2.875em;
  .text{
    margin-top: 2.31em;
    line-height: 233%;
    font-weight: 600;
    h3{
      font-size: 1.125em;
      line-height: 156%;
      color: #000;
      font-weight: 600;
    }
    p,li{
      font-size: .75em;
      line-height: 233%;
      margin-bottom: 0;
    }
    hr{
      height: 2em;
    }
  }
}


@media screen and (max-width: 992px){
  .become-partner{
    &__info{
      grid-template-columns: 20em calc(100% - 21.5em);
      gap: 1em 1.5em;
    }
    .text{
      margin-top: 1.5em;
    }
  }

  .conf{
    .text{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .become-partner{
    &__desc{
      font-size: .875em;
    }
    &__info{
      grid-template-columns: 100%;
    }
  }

  .text-big{
    p,li{
      font-size: .875em;
    }
  }

  .conf{
    .h2{
      font-size: 1.75em;
    }
  }
}