.about{
  padding: 1.19em 0 11em;
  &__desc{
    margin-top: .875em;
  }
  &__grid{
    margin-top: 2.31em;
    display: grid;
    grid-template-columns: 18.4375em calc(100% - 18.4375em - 1.25em);
    gap: 2em 1.25em;
  }
}

.about-nav{
  position: sticky;
  top: 1em;
  border-radius: 0.75em;
  background-color: $white;
  box-shadow: $shadow2;
  &__group{
    &:not(:first-child){
      .about-nav__top{
        padding-top: 0;
      }
    }
    &--dynamic{
      padding-bottom: 1.3em;
      .about-nav__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        &:after{
          content: '';
          background-image: url(../img/arrow-bottom.svg);
          @include bgImgSize(100%);
          width: 1em;
          height: 1em;
          flex-shrink: 0;
          margin-left: .5em;
          transition: all .3s;
        }
      }
      .about-nav__title{
        font-size: 1em;
      }
      .about-nav__bottom{
        display: none;
        padding-bottom: 0;
        padding-top: .5em;
      }
      &._active{
        .about-nav__top:after{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__top{
    padding: 1.875em 1.875em 0;
  }
  &__bottom{
    padding: 1.3em 0;
  }
  &__title{
    line-height: 140%;
    color: $dark;
  }
  &__links{
    @include grid100gap(.12em);
  }
  &__link{
    min-height: 2.875em;
    display: flex;
    align-items: center;
    padding: .69em 1.875em;
    transition: all .3s;
    span{
      font-size: .875em;
      line-height: 185%;
    }
    &:hover{
      cursor: pointer;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: $white;
    }
  }
}

.about-reviews{
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .75em));
    gap: 2.17em 1em;
    margin-top: 1.56em;
  }
  &__nav{
    margin-top: 4.5em;
  }
}

._small-certificates{
  .certificate-card{
    padding: .5em;
    &__inner{
      padding: .5em;
    }
  }
}

.requisites{
  .requisites-group{
    margin-top: 2.375em;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 2em;
  }
}

.requisites-group{
  &__items{
    margin-top: 1.25em;
  }
}

.requisite-item{
  display: grid;
  grid-template-columns: 12em calc(100% - 12.5em);
  gap: .5em;
  border-bottom: 1px solid #E8EBF6;
  padding: .56em 0;
  min-height: 2.875em;
  position: relative;
  &:last-child{
    border-bottom: none;
  }
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 130%;
    word-break: break-word;
  }
  &__title{
    color: #84A3C9;
  }
  &__desc{
    color: $dark;
  }
  .m-copy{
    position: absolute;
    right: 1.125em;
    top: .875em;
    &~.requisite-item__desc{
      padding-right: 2.5em;
    }
  }
}

.hint{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    padding: .625em 1em .625em .625em;
    background: #181C1D;
    color: $white;
    border-radius: .5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%) rotate(45deg);
      bottom: -.1875em;
      background: inherit;
      width: .5em;
      height: .5em;
    }
  }
  .text14{
    line-height: 157%;
  }
  .text12{
    line-height: 189%;
  }
}

.m-copy{
  position: relative;
  &._open{
    .hint{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    width: .75em;
    @include center;
    img{
      width: 100%;
    }
  }
  .hint{
    position: absolute;
    left: 50%;
    bottom: 100%;
    transform: translateX(-50%);
    padding-bottom: 1.25em;
    &__inner{
      display: flex;
      align-items: center;
      background: #0b1011;
      height: 2.75em;
      padding: 0 1em 0 .625em;
      img{
        width: 1.5em;
        margin-right: .25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    padding-bottom: 4em;
    &__desc{
      font-size: .875em;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 15em calc(100% - 16.25em);
    }
  }

  .about-reviews{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .requisite-item{
    .hint{
      right: -2em;
      left: unset;
      transform: unset;
      &__inner{
        &:before{
          right: 2em;
          left: unset;
          transform: rotate(45deg);
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .about-nav{
    &__top{
      padding: 1.25em 1.25em 1em;
    }
    &__bottom{
      padding: 0 0 1em;
      //display: none;
    }
  }

  .requisites{
    .requisites-group{
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
    }
  }

  .requisites-group{
    &__items{
      margin-top: .25em;
    }
  }

  .requisite-item{
    grid-template-columns: calc(40% - .375em) calc(60% - .375em);
    gap: .75em;
    min-height: 2em;
    &__title,
    &__desc{
      font-size: .75em;
    }
    .m-copy{
      top: .625em;
      right: 0;
    }
  }
}