@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/_filter-slider.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt60{
  margin-top: 1.75em;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 3.4375em;
    height: 3.4375em;
    @include lastM(0 .75em 0 0);
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: #4D77C8;
      }
    }
  }
}

.animated-opacity{
  &.animated{
    opacity: 0;
    transform: translateY(80px);
    transition: transform 1s, opacity 1s,color .3s;
    &--active{
      transform: translateY(0);
      opacity: 1;
    }
  }
}

.m-section{
  padding: 3.125em 0;
  &--white{
    background-color: $white;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.5em 0;
  }

  ._mt24-mob{
    margin-top: 1.5em;
  }

  ._mt16-mob{
    margin-top: 1em;
  }
}


