.services{
  padding: 1.19em 0 3.81em;
  &__top{
    margin-top: .875em;
  }
  &__desc{
    max-width: 32em;
  }
  &__grid{
    margin-top: 2em;
    display: grid;
    grid-template-columns: 18.4375em calc(100% - 18.4375em - 1.25em);
    gap: 2em 1.25em;
  }
  &__content{
    @include card;
    padding: 1.69em 2em;
  }
  &__images{
    display: grid;
    grid-template-columns: 54.1% 43.8%;
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    display: block;
  }
  .photo-slider{
    margin-top: 1.75em;
  }
}

.services-nav{
  &__link{
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    padding: 1.875em .94em;
    @include card;
    color: $dark;
    &._active,
    &:hover{
      color: #3E98D9;
    }
    &:last-child{
      margin: 0;
    }
    &-img{
      width: 5.625em;
      margin-right: .625em;
    }
    &-text{
      font-size: .875em;
      line-height: 157%;
    }
  }
}

.services-info{
  margin-top: 1.75em;
  .text{
    margin-top: .94em;
    max-width: 32.5em;
    color: #000;
  }
}


@media screen and (max-width: 992px){
  .services{
    padding: 1.19em 0 3.81em;
    &__top{
      align-items: flex-start;
      flex-direction: column;
    }
    &__desc{
      font-size: .875em;
      max-width: 100%;
    }
    &__btn{
      margin-top: 1em;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
    }
    &__col{
      position: relative;
      z-index: 2;
    }
    &__content{
      padding: 1.25em;
      width: calc(100% + 2em);
      margin-left: -1em;
    }
  }

  .services-nav{
    display: flex;
    @include fullScroll;
    &__link{
      flex-shrink: 0;
      width: 15em;
      margin: 0 .75em 0 0;
      padding: .75em;
      &-img{
        width: 3em;
      }
      &-text{
        font-size: .75em;
      }
    }
  }

  .services-info{
    .text{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .services{
    &__images{
      grid-template-columns: 100%;
      gap: .75em;
    }
  }
}