.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #D7DCDD;
    border: none;
    box-shadow: none;
    height: .1875em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary2;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary2;
      width: .9375em;
      height: .9375em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: 0 0 .75em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5em;
  }
  .fg{
    position: relative;
    input{
      height: 3.5em;
      color: #3E98D9;
      font-weight: 600;
      font-size: .875em;
      line-height: 140%;
      padding: 0 1.25em 0 .25em;
      border-radius: .43em;
      text-align: right;
    }
    &__currency{
      position: absolute;
      right: .6em;
      top: 50%;
      transform: translateY(-44%);
      font-size: .875em;
      line-height: 140%;
      font-weight: 600;
      color: #3E98D9;
    }
  }
}
