.header{
  transition: all .3s;
  position: sticky;
  z-index: 10;
  top: 0;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 10.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__catalog-btn{
    margin-right: 1.5em;
  }
  &__cart{
    width: 3.375em;
    padding: 0;
    position: relative;
    margin-left: 1em;
    &-num{
      position: absolute;
      width: 1.625em;
      height: 1.625em;
      background-color: $yellow;
      border-radius: 50%;
      color: $white;
      top: -.3125em;
      right: -.4375em;
      @include center;
      span{
        font-size: .875em;
      }
    }
    img{
      margin: 0;
    }
  }
  &._fix{
    background-color: $white;
    .header-bottom{
      //padding: 1em 0;
    }
  }
}

.burger{
  display: none;
}

.header-top{
  background-color: $white;
  &__bg,
  &__close{
    display: none;
  }
}

.header-bottom{
  padding: 1em 0;
  transition: all .3s;
}

.lang__show,
.nav-item__show,
.nav__link{
  height: 2.5em;
  display: flex;
  align-items: center;
  color: #3A526D;
  position: relative;
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: .125em;
    background-color: $primary;
    transition: all .3s;
  }
  &:hover{
    cursor: pointer;
    color: $primary;
    path{
      fill: $primary;
    }
    &:before{
      width: 100%;
    }
  }
  span{
    font-size: 0.875em;
  }
  svg{
    width: .625em;
    margin-left: .25em;
  }
}

.nav{
  display: flex;
  &__link{
    margin-right: 2.44em;
  }
}

.nav-item{
  margin-right: 2.44em;
  position: relative;
  ul{
    color: #3A526D;
    @include grid100gap(.625em);
    a{
      font-size: .875em;
      line-height: 120%;
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .nav-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      margin-top: 1px;
      padding-top: 1px;
      left: 0;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      z-index: 3;
      white-space: nowrap;
      li{
        position: relative;
        &:hover{
          ul{
            opacity: 1;
            pointer-events: unset;
          }
        }
        &:before{
          content: '';
          position: absolute;
          right: -1em;
          transform: translateX(1px);
          top: 0;
          width: 1.5em;
          height: 100%;
        }
      }
      ul ul{
        position: absolute;
        top: 0;
        left: 100%;
        padding: 1em;
        min-width: 12em;
        background-color: $white;
        box-shadow: $shadow;
        border-radius: .5em;
        margin-left: .9375em;
        transition: all .3s;
        opacity: 0;
        pointer-events: none;

      }
    }
    &__inner{
      padding: 1em;
      min-width: 12em;
      background-color: $white;
      box-shadow: $shadow;
      border-radius: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__cart{
      margin-left: .5em;
    }
    &__catalog-btn{
      display: none;
    }
    &__catalog-btn,
    &__call{
      width: 3.375em;
      padding: 0;
      img{
        margin: 0;
      }
      span{
        display: none;
      }
    }
    &._open{
      .header-top{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .header-bottom{
    padding: 1.25em 0;
    &__inner{
      padding-bottom: 4em;
      position: relative;
    }
  }

  .burger{
    display: flex;
    margin-left: .5em;
    padding: 0;
    width: 3.375em;
  }

  .header-top{
    position: fixed;
    top: 0;
    left: 0;
    width: 20em;
    height: $vh;
    overflow-y: auto;
    z-index: 20;
    padding: 3em 0 2em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg{
      z-index: 4;
    }
    &__close{
      position: fixed;
      top: 1em;
      right: 3.5em;
      display: flex;
      width: 2.5em;
      height: 2.5em;
      padding: 0;
      border-radius: .375em;
      img{
        width: 1em;
      }
    }
    &__inner{
      flex-direction: column;
      align-items: flex-start;
    }
    .lang{
      order: -1;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    &__link{
      margin-right: 0;
      &:before{
        display: none;
      }
    }
  }

  .nav-item{
    margin-right: 0;
    width: 100%;
    &._open{
      .nav-item__show svg{
        transform: rotate(-180deg);
      }
    }
    &__show{
      justify-content: space-between;
      &:before{
        display: none;
      }
    }
    &__hidden{
      display: none;
      padding-left: 1em;
    }
    ul ul{
      padding: 1em 0 .5em 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 8em;
    }
    &__cart{
      &-num{
        font-size: .75em;
      }
    }
  }

  .burger,
  .header__call,
  .header__cart{
    border-radius: .375em;
    width: 2.5em;
    height: 2.5em;
  }
}
