.page-top{
    padding: 1em 0 0;
}

.breadcrumbs {
    margin-bottom: 1.19em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $dark;
        font-weight: 500;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '/';
                position: absolute;
                z-index: 1;
                top: -.125em;
                right: 100%;
                margin-right: .375em;
                font-size: 1.125em;
                line-height: 155%;
                color: #84A3C9;
            }
        }
        &:last-child{
            a{
                color: #84A3C9;
                text-decoration: none;
            }
        }
    }
    a{
        font-size: 1.125em;
        line-height: 155%;
        text-decoration: underline;
    }
}




@media screen and (max-width: 992px){
    .breadcrumbs{
        font-size: .875em;
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        font-size: .75em;
    }
}
