.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        width: 77.3125em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.31em 2.75em;
        border-radius: .75em;
        display: inline-block;
        box-shadow: $shadow2;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        position: absolute;
        bottom: 100%;
        left: 100%;
        margin-left: .1875em;
    }
    .header-search{
        width: 100%;
        position: relative;
        inset: unset;
    }
}

.close-btn{
    background-color: transparent;
    width: 1.8125em;
    @include center;
    svg{
        width: 100%;
    }
}

.header-catalog{
    color: $dark;
    &__grid{
        margin-top: 1.5em;
    }
    &__title{
        font-size: 1.375em;
        line-height: 127%;
    }
    &__links{
        margin-top: 1.375em;
        ul{
            @include grid100gap(.625em);
        }
        a{
            font-size: .875em;
            line-height: 200%;
        }
    }
}

.call-modal{
    .modal__dialog{
        width: 27.375em;
        padding: 2em 1.875em;
    }
    .close-btn{
        width: 1.125em;
    }
}

.modal-form{
    margin-top: 1.75em;
    &__fields{
        @include grid100gap(1.5625em);
    }
    .m-check{
        margin-top: .625em;
        label{
            min-height: 1.5em;
            &:before{
                width: 1.5em;
                height: 1.5em;
                border-radius: .625em;
            }
            span{
                font-size: .625em;
            }
        }
    }
    &__btn{
        margin-top: 1.75em;
    }
}

.model-modal{
    .modal__dialog{
        width: 60em;
    }
    &__inner{
        padding-bottom: 50%;
        position: relative;
        canvas{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }

    .header-catalog{
        display: none;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
        .close-btn{
            left: unset;
            right: 0;
            margin-bottom: .25em;
        }
    }


    .call-modal{
        .modal__dialog{
            padding-left: 1.25em;
            padding-right: 1.25em;
        }
    }
}
