.text{
  line-height: 175%;
  font-weight: 300;
  h1,
  h2,
  h3,
  h4{
    color: $dark;
  }
  h3{
    margin-bottom: .67em;
  }
  p{
    margin-bottom: 1.5em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    padding-left: .625em;
    li{
      position: relative;
      padding-left: .75em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  a{
    text-decoration: underline;
  }
  hr{
    height: 3.125em;
    display: block;
    opacity: 0;
    margin: 0;
  }
  img{
    width: 100%;
    border-radius: .75em;
    display: block;
  }
  .video{
    text-decoration: unset;
  }
}



@media screen and (max-width: 992px){
  .text{
    hr{
      height: 2em;
    }
  }
}


