.partners{
  &__grid{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1em 2em;
  }
}

.team{
  .photo-slider{
    margin-top: 1.375em;
  }
  &__items{
    row-gap: 3em;
  }
}

.photo-slider{
  overflow: hidden;
  border-radius: .755em;
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      padding-right: 1em;
    }
    &-pagination{
      position: absolute;
      bottom: 1.75em;
    }
    &-button{
      &-prev{
        left: 4em;
      }
      &-next{
        right: 6em;
      }
    }
  }
  &__img{
    @include imgCover(55%);
    border-radius: .75em;
  }
}

.team-card{
  padding: 1.875em;
  border-radius: 0.75em;
  background: $white;
  box-shadow: $shadow2;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: $dark;
  &__img{
    @include imgCover(100%);
    padding-bottom: 0;
    width: 10.5625em;
    height: 10.5625em;
    border-radius: 50%;
    margin-bottom: 1.875em;
  }
  &__prof{
    margin-top: .67em;
    font-size: .9375em;
    line-height: 186%;
    color: #84A3C9;
  }
  &__phone{
    font-size: 1.4375em;
    line-height: 122%;
    font-weight: 600;
    margin-top: .45em;
  }
  &__email{
    margin-top: .625em;
  }
}

.ceo{
  display: grid;
  grid-template-columns: calc(100% - 27.3125em - 1.4375em) 27.3125em;
  justify-content: space-between;
  &__img{
    width: 100%;
    display: block;
    border-radius: .75em;
  }
  &__prof{
    margin-top: 1em;
  }
  &__desc{
    margin-top: 1.6875em;
  }
}

.about-info{
  margin-top: 4.75em;
}

.how-work{
  margin-top: 1.875em;
  max-width: 32.5em;
}

.about-reviews2{
  margin-top: 2.375em;
}

.vacancies{
  &__items{
    margin-top: 1.25em;
    @include grid100gap(2.5em);
  }
  &__nav{
    margin-top: 4.94em;
  }
}

.vacancy-card{
  border-radius: 0.75em;
  background: $white;
  box-shadow: $shadow;
  padding: 1.875em;
  @include grid100gap(.625em);
  color: $dark;
  &__salary{
    font-size: .9375em;
    line-height: 187%;
    color: #84A3C9;
  }
}


@media screen and (max-width: 992px){
  .partners{
    &__grid{
      grid-template-columns: repeat(3, 1fr);
      gap: 1em;
    }
  }


  .team{
    .photo-slider{
      margin-top: 1em;
    }
    &__items{
      row-gap: 2em;
      margin-top: 2em;
    }
  }

  .photo-slider{
    .swiper{
      &-pagination{
        bottom: .75em;
      }
      &-button{
        display: none;
      }
    }
    &__img{
      padding-bottom: 80%;
    }
  }

  .team-card{
    padding: 1.25em;
    font-size: .875em;
  }

  .ceo{
    grid-template-columns: 100%;
    gap: 1em;
    &__col2{
      order: -1;
      max-width: 20em;
    }
    &__prof{
      margin-top: .5em;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .vacancies{
    &__items{
      margin-top: 1em;
      gap: 1em;
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .vacancy-card{
    padding: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .partners{
    &__grid{
      margin-top: .5em;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  .team{
    &__items{
      row-gap: 1em;
      margin-top: 1.5em;
    }
  }
}