.slider{
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.swiper-button{
  width: 3.375em;
  height: 3.375em;
  @include center;
  border-radius: .75em;
  background-color: $white;
  svg{
    width: .625em;
  }
  &:hover{
    box-shadow: 0px 15px 15px 0px rgba(47, 42, 133, 0.07);
    path{
      stroke: $yellow;
    }
  }
}

.swiper-pagination{
  z-index: 2;
  margin-top: 3.75em;
  position: static;
  @include center;
  &-bullet{
    width: .5em;
    height: .5em;
    background-color: #84A3C9;
    opacity: .38;
    border-radius: 0.9375em;
    margin: 0 .375em !important;
    &-active{
      opacity: 1;
      width: 1.875em;
      background-color: $yellow;
    }
  }
}

.news-slider,
.certificates-slider,
.engineering-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        gap: 1.25em;
        grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
        transform: unset !important;
      }
      &-slide{
        width: 100%;
      }
    }
  }
}

.engineering-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-radius: 0.75em;
  background: $white;
  padding: 1.875em;
  box-shadow: 0px 15px 15px 0px rgba(47, 42, 133, 0.03);
  transition: all .3s;
  &:hover{
    box-shadow: 0px 15px 15px 0px rgba(47, 42, 133, 0.07);
  }
  &__img{
    height: 10.625em;
    @include center;
    margin-bottom: 2em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 155%;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
    display: flex;
    justify-content: center;
  }
}

.clients-slider{
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: calc(100% / 6);
      padding-right: 1.25em;
    }
    &-button{
      margin: 0;
      top: 3.125em;
      transform: translateY(-50%);
      &-prev{
        left: -9.375em;
      }
      &-next{
        right: -9.375em;
      }
    }
  }
}

.client-card{
  &__img{
    height: 6.25em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .swiper-pagination{
    margin-top: 1.5em;
  }

  .news,
  .certificates,
  .engineering{
    overflow: hidden;
  }

  .news-slider,
  .certificates-slider,
  .engineering-slider{
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 19em;
        padding-right: 1em;
      }
    }
  }

  .engineering-card{
    box-shadow: 0px 15px 15px 0px rgba(47, 42, 133, 0.07);
  }

  .clients{
    overflow: hidden;
  }

  .clients-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 14.125em;
    }
    .swiper-button{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .clients-slider{
    .swiper-container{
      font-size: .875em;
    }
  }
}
