@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.4375em;
    height: 3.375em;
    border-radius: .75em;
    &:focus {
        box-shadow: none;
    }
    img{
        width: 1.25em;
        margin: 0 .625em;
        &:first-child{
            margin-left: 0;
        }
        &:last-child{
            margin-right: 0;
        }
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-yellow{
    background-color: $yellow;
    color: $white;
    &:hover{
        background-color: darken($yellow,10%);
        color: $white;
    }
}

.m-btn-yellow-outline{
    color: $yellow;
    border: 1px solid $yellow;
    &:hover{
        background-color: $yellow;
        color: $white;
        box-shadow: 0px 7px 16px 0px rgba(239, 204, 150, 0.50);
    }
}